/* EMAIL NOTIFICATONS SETTINGS */
.switcher {
  position: absolute;
  width: 150px;
  height: 50px;
  text-align: center;
  background: #4cd964;
  transition: all 0.2s ease;
  border-radius: 25px;
}
.switcher span {
  position: absolute;
  width: 20px;
  height: 4px;
  top: 50%;
  left: 50%;
  margin: -2px 0px 0px -4px;
  background: #fff;
  display: block;
  -webkit-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  transform: rotate(-45deg);
  transition: all 0.2s ease;
  border-radius: 2px;
}
.switcher span:after {
  content: "";
  display: block;
  position: absolute;
  width: 4px;
  height: 12px;
  margin-top: -8px;
  background: #fff;
  transition: all 0.2s ease;
  border-radius: 2px;
}
input[type=radio] {
  display: none;
}
.switcher label {
  cursor: pointer;
  color: rgba(0, 0, 0, 0.2);
  width: 60px;
  line-height: 50px;
  transition: all 0.2s ease;
}
label[for=first_yes] {
  position: absolute;
  left: 0px;
  height: 20px;
}
label[for=first_no] {
  position: absolute;
  right: 0px;
}
#first_no:checked ~ .switcher {
  background: #ff3b30;
}
#first_no:checked ~ .switcher span {
  background: #fff;
  margin-left: -8px;
}
#first_no:checked ~ .switcher span:after {
  background: #fff;
  height: 20px;
  margin-top: -8px;
  margin-left: 8px;
}
#first_yes:checked ~ .switcher label[for=first_yes] {
  color: #fff;
}
#first_no:checked ~ .switcher label[for=first_no] {
  color: #fff;
}
